<template>
  <div>
    <div style="text-align: center">
      <h2>操作系统课设 - 磁盘空间管理</h2>
      <h3>作者：李锴</h3>
    </div>
    <div class="box ddl-box">
      <div class="box-m">
        <h3>已分配文件列表</h3>
        <ul>
          <li v-for="item,index in dolist" :key="index">
            <i class="el-icon-document"></i>文件名: {{item.name}}
            <div style="margin-top: 15px;"><el-tag>文件大小： {{item.size}}</el-tag></div>
            <div style="margin: 18px 0">
              <span>起始位置：{{item.start}}</span>
              <span style="margin-left: 25px;">结束位置：{{item.end}}</span>
            </div>
          </li>
        </ul>
      </div>
      <div class="box-r">
        <h3>磁盘使用情况</h3>
        <p><span class="el-icon-date"></span>  总容量：{{disk.oSize}} KB</p>
        <p><span class="el-icon-date"></span>  已使用：{{disk.oSize - disk.lSize}} KB</p>
        <p><span class="el-icon-date"></span>  剩余容量：{{disk.lSize}} KB</p>

        <div style="width: 90%;margin: 0 auto;margin-top: 50px;">
          <h4>当前使用进度：</h4>
          <el-progress :text-inside="true" :stroke-width="18" :percentage="percent"></el-progress>
        </div>


      </div>
    </div>

    <div class="box-l">
      <h3>选择操作</h3>
      <el-row>
        <el-col :span="12">
          <el-tag class="title-tag">创建文件</el-tag>
          <el-row>
            <el-col :span="8"  style="margin-bottom: 8px;">
              <span>文件名称：</span>
              <el-input class="input-item" v-model="filename"></el-input>
            </el-col>
            <el-col :span="16">
              <span>文件大小：</span>
              <el-input v-model="filesize" class="input-item"></el-input> <i style="color: red;">KB</i>
            </el-col>
          </el-row>
          <el-col style="margin-bottom: 30px;margin-top: 15px;">
            <el-button type="primary" size="mini" @click="addFile">点击创建文件</el-button>
          </el-col>
        </el-col>
        <el-col :span="12">
          <el-tag  class="title-tag" type="success">回收文件</el-tag>
          <el-col>
            <span>文件名称：</span>
            <el-input v-model="recallname" class="input-item"></el-input>
          </el-col>
          <el-col style="margin-bottom: 30px;margin-top: 15px;">
            <el-button type="success" size="mini" @click="recall">点击回收内存</el-button>
          </el-col>
        </el-col>
      </el-row>

    </div>

    <div class="box">
      <h3>位示图（ 50 x 512）</h3>
      <span v-for="item,index in list" :key="index">
        <span class="rec-item" v-if="item == '0'">{{item}}</span>
        <span class="rec-item red" v-else>{{item}}</span>
    </span>
    </div>

  </div>
</template>

<script>
  import axios from 'axios'
  import qs from 'qs'
  let echarts = require('echarts/lib/echarts');
  // 引入饼图组件
  require('echarts/lib/chart/pie');
  // 引入提示框和图例组件
  require('echarts/lib/component/title');
  require('echarts/lib/component/tooltip');
  require('echarts/lib/component/legend');
  require("echarts/lib/component/legendScroll");


  export default {
    data() {
      return {
        filename: '',
        filesize: 0,
        recallname: '',
        list: [],
        dolist: [],
        disk: [],
        percent: 0
      }
    },
    mounted: function () {
    },
    created() {
      this.list = []
      this.dolist = []
      this.disk = []
      axios.post('/space/init', {})
        .then((response) => {
          console.log(response.data);
          this.list = response.data.list
          this.dolist = response.data.docList
          this.disk = response.data.disk
        })
        .catch((error) => {
          console.log(error);
          alert('初始化内存失败！！')
        })
    },
    methods: {
      toPercent(point){
        var str=Number(point*100).toFixed(1);
        return str*1.0;
      },
      addFile() {
        const obj = {
          'name': this.filename,
          'size': this.filesize
        }
        axios.post('/space/add', qs.stringify(obj))
          .then((response) => {
            this.$message({
              message: '创建成功!!!',
              type: 'success'
            });
            this.list = response.data.list
            this.dolist = response.data.docList
            this.disk = response.data.disk

            this.percent  = this.toPercent((this.disk.oSize - this.disk.lSize)/this.disk.oSize)
            console.log(response.data);
          })
          .catch((error) => {
            this.$message({
              message: error.response.data.message,
              type: 'warning'
            });
          });
      },
      recall() {
        const obj = {
          'name': this.recallname
        }
        axios.post('/space/delete', qs.stringify(obj))
          .then((response) => {
            this.$message({
              message: '回收成功!!!',
              type: 'success'
            });
            this.list = response.data.list
            this.dolist = response.data.docList
            this.disk = response.data.disk
            this.percent = this.toPercent((this.disk.oSize - this.disk.lSize)/this.disk.oSize)
            console.log(response.data);
          })
          .catch((error) => {
            this.$message({
              message: error.response.data.message,
              type: 'warning'
            });
          });
      }
    }
  }
</script>

<style>
  .rec-item {
    font-size: 14px;
    display:inline-block;
    width: 24px;
    height: 24px;
    background: #4EEE94;
    text-align: center;
    line-height: 30px;
    border: 1px solid #555;
    transition: background-color 1.5s;
  }
  .box {
    width: 1300px;
    margin: 0 auto;
    transition: background-color 1.5s ease;
    margin-top: 80px;
  }
  .ddl-box {
    display: flex;
    margin-bottom: 50px;
    min-height: 400px;
  }
  .box-l {
    flex: 2;
    width: 1180px;
    padding: 10px 40px;
    margin: 0 auto;
    border: 1px solid #777;
    border-radius: 5px;
    box-shadow: 5px 5px 2px #ccc;
  }
  .ddl-box .box-m {
    overflow-y: scroll;
    flex: 3;
    padding: 10px;
    margin: 0 10px;
    border: 1px solid #777;
    border-radius: 5px;
    box-shadow: 5px 5px 2px #ccc;
    height: 400px;
    max-height: 400px;
  }
  .ddl-box .box-m h3 {
    margin-left: 48px;
  }
  .ddl-box .box-m ul {
    width: 60%;
    margin: 0 auto;
    padding-bottom: 20px;
  }
  .ddl-box .box-r {
    height: 400px;
    flex: 3;
    padding: 10px;
    margin: 0 10px;
    border: 1px solid #777;
    border-radius: 5px;
    box-shadow: 5px 5px 2px #ccc;
  }
  .ddl-box .box-r h3 {
    margin-left: 18px;
  }
  .ddl-box .box-r p {
    margin-left: 38px;
  }
  /* commoncss */
  .input-item {
    width: 80px!important;
    padding: 2px;
  }

  .el-input__inner {
    height: 30px!important;
  }

  .red {
    transition: background-color 1.5s ease;
    background-color: #FF4040;
  }

  .title-tag {
    height: 25px!important;
    line-height: 25px;
    width: 90px;
    text-align: center;
    margin-bottom: 8px;
  }
</style>




